
import dropdown from "../dropdownComp.vue"
import swapIcon from "../icons/swapIcon.vue"
import { mapGetters,mapMutations } from 'vuex'

export default {
    components:{
        dropdown,
        swapIcon 
    },
    computed:{ 
      ...mapGetters([
        'curFrom',
        'curTo',
        'amountFrom',
        'amountTo',
        'getCurs',
        'currencies',
      ]),
      cursTo(){
        return this.getCurs;
      }

    },
    methods :{
      ...mapMutations(['setAmountFrom','setCurFrom','setCurTo','swapCurr'])
    }


}

